import React, { useEffect } from 'react'
import { svg_service } from '../../../../../services/svgService'
import * as svg from "../../../../../utils/svg"

interface Props {
    svgData: {
        _id: string
        svgURL: string
        svgString: string
        tags: string[]
        created_at: string
        updated_at: string
    }
}

const RenderSvg: React.FC<Props> = ({ svgData }) => {

    const svgUrl = svg.svgToURL(svgData.svgString)

    return (
        <div className="flex justify-between mb-1">
            <button className="border border-gray95" style={{ width: 100, height: 100 }} >
                <img alt="svg" src={svgUrl} />
            </button>
            <button className="bg-redish hover:bg-red600 text-white font-bold py-2 px-4 rounded">Delete</button>
        </div>
    )
}

export default RenderSvg
